import React from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Button,
  Chip,
  Rating,
  IconButton,
  useTheme,
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import {
  ArrowForward,
  FavoriteBorder,
  Favorite,
  LocalShipping,
  Star,
} from '@mui/icons-material';
import { products, categories } from '../data/products';

const HeroSection = () => {
  const theme = useTheme();
  
  return (
    <Box
      sx={{
        bgcolor: 'primary.light',
        py: { xs: 8, md: 12 },
        position: 'relative',
        overflow: 'hidden',
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4} alignItems="center">
          <Grid item xs={12} md={6}>
            <Typography
              variant="h1"
              sx={{
                mb: 2,
                fontWeight: 800,
                fontSize: { xs: '2.5rem', md: '3.5rem' },
                lineHeight: 1.2,
                color: theme.palette.text.primary,
              }}
            >
              Official Trump<br />
              Merchandise Store
            </Typography>
            <Typography
              variant="h6"
              color="text.secondary"
              sx={{ mb: 4, maxWidth: 500 }}
            >
              Discover our premium collection of authentic Trump merchandise that celebrates American greatness.
            </Typography>
            <Button
              component={RouterLink}
              to="/categories"
              variant="contained"
              size="large"
              endIcon={<ArrowForward />}
              sx={{
                py: 1.5,
                px: 4,
                fontSize: '1.1rem',
                boxShadow: theme.shadows[3],
              }}
            >
              Shop Now
            </Button>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box
              sx={{
                display: 'grid',
                gridTemplateColumns: 'repeat(2, 1fr)',
                gap: 2,
                mt: { xs: 4, md: 0 },
              }}
            >
              {categories.slice(0, 4).map((category) => (
                <Card
                  key={category.id}
                  sx={{
                    height: 200,
                    position: 'relative',
                    overflow: 'hidden',
                    transition: 'transform 0.3s ease',
                    '&:hover': {
                      transform: 'translateY(-4px)',
                      boxShadow: theme.shadows[8],
                    },
                  }}
                >
                  <CardMedia
                    component="img"
                    image={category.image}
                    alt={category.name}
                    sx={{
                      height: '100%',
                      transition: 'transform 0.3s ease',
                    }}
                  />
                  <Box
                    sx={{
                      position: 'absolute',
                      bottom: 0,
                      left: 0,
                      right: 0,
                      p: 2,
                      background: 'linear-gradient(transparent, rgba(0,0,0,0.7))',
                    }}
                  >
                    <Typography variant="h6" color="white">
                      {category.name}
                    </Typography>
                  </Box>
                </Card>
              ))}
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

const FeaturedProducts = () => {
  const [favorites, setFavorites] = React.useState([]);
  const theme = useTheme();

  const toggleFavorite = (productId) => {
    setFavorites(prev => 
      prev.includes(productId)
        ? prev.filter(id => id !== productId)
        : [...prev, productId]
    );
  };

  return (
    <Box sx={{ py: 8 }}>
      <Container maxWidth="lg">
        <Typography 
          variant="h2" 
          gutterBottom
          sx={{
            textAlign: 'center',
            color: theme.palette.text.primary,
            fontWeight: theme.typography.fontWeightBold,
            mb: 6,
          }}
        >
          Featured Products
        </Typography>
        <Grid container spacing={3}>
          {products.slice(0, 6).map((product) => (
            <Grid item key={product.id} xs={12} sm={6} md={4}>
              <Card
                sx={{
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  position: 'relative',
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: theme.shadows[8],
                  },
                }}
              >
                {product.bestseller && (
                  <Chip
                    icon={<Star sx={{ fontSize: 16 }} />}
                    label="Bestseller"
                    size="small"
                    sx={{
                      position: 'absolute',
                      left: 12,
                      top: 12,
                      zIndex: 1,
                      bgcolor: '#FFD700',
                      color: '#000',
                      fontWeight: 600,
                    }}
                  />
                )}
                
                <IconButton
                  onClick={() => toggleFavorite(product.id)}
                  sx={{
                    position: 'absolute',
                    right: 8,
                    top: 8,
                    bgcolor: 'white',
                    zIndex: 1,
                    '&:hover': { 
                      bgcolor: 'white', 
                      transform: 'scale(1.1)',
                      boxShadow: theme.shadows[2],
                    },
                    transition: 'transform 0.2s ease',
                  }}
                >
                  {favorites.includes(product.id) ? (
                    <Favorite color="primary" />
                  ) : (
                    <FavoriteBorder />
                  )}
                </IconButton>

                <CardMedia
                  component="img"
                  height="300"
                  image={product.image}
                  alt={product.name}
                  sx={{
                    objectFit: 'cover',
                    transition: 'transform 0.3s ease',
                    '&:hover': {
                      transform: 'scale(1.05)',
                    },
                  }}
                />

                <CardContent sx={{ flexGrow: 1, pt: 2 }}>
                  <Box sx={{ mb: 1, display: 'flex', gap: 1 }}>
                    <Chip
                      label={product.category}
                      size="small"
                      sx={{
                        bgcolor: theme.palette.primary.light,
                        color: theme.palette.primary.main,
                        fontWeight: 600,
                      }}
                    />
                    {product.exclusive && (
                      <Chip
                        label="Exclusive"
                        size="small"
                        sx={{
                          bgcolor: theme.palette.secondary.main,
                          color: 'white',
                          fontWeight: 600,
                        }}
                      />
                    )}
                  </Box>

                  <Typography
                    component={RouterLink}
                    to={`/product/${product.id}`}
                    variant="h6"
                    sx={{
                      textDecoration: 'none',
                      color: theme.palette.text.primary,
                      '&:hover': { color: theme.palette.primary.main },
                    }}
                  >
                    {product.name}
                  </Typography>

                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, mt: 1 }}>
                    <Rating value={product.rating} precision={0.1} readOnly size="small" />
                    <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                      ({product.reviews})
                    </Typography>
                  </Box>

                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                    <LocalShipping sx={{ color: theme.palette.text.secondary, fontSize: 20 }} />
                    <Typography variant="body2" color="text.secondary">
                      {product.shipping}
                    </Typography>
                  </Box>

                  <Typography variant="h6" color="primary" sx={{ fontWeight: 600 }}>
                    ${product.price}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
        
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <Button
            component={RouterLink}
            to="/products"
            variant="outlined"
            color="primary"
            endIcon={<ArrowForward />}
            size="large"
            sx={{
              borderWidth: 2,
              '&:hover': {
                borderWidth: 2,
                boxShadow: theme.shadows[2],
              },
            }}
          >
            View All Products
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

const Home = () => {
  return (
    <>
      <HeroSection />
      <FeaturedProducts />
    </>
  );
};

export default Home;
